// FOOTER :: START

.go-top{
	position: fixed;
	bottom: 6em;
	right: 2em;
	text-decoration: none;
	color: #fff;
	background-color: rgba(120, 120, 120, 0.3);
	font-size: 16px;
	padding: 10px;
	display: none;
	z-index: 999999;
	height: 48px;
	width: 48px;
	border-radius: 3px;
	cursor: pointer;

	&:hover{
		background-color: rgba(167, 167, 167, 0.3);
	}
}
body.chatbot_active{
	.go-top{
		display: none;
	}
}
.light {
	.go-top{
		background-color: rgba(0,0,0,.3);

		&:hover{
			background-color: rgba(0,0,0,.6);
			text-decoration: none;
		}
	}
}

.footer {
	display: flex;
	flex-direction: column;

	&__wrapper {
		&-1 {
			display: flex;
			padding-top: 30px;
			padding-bottom: 30px;
			@include adaptive(767) {
				padding-bottom: 15px;
			}
		}
		&-2 {
			display: flex;
			align-items: center;
			min-height: 55px;
			padding-top: 10px;
			padding-bottom: 10px;
			background-color: #081b49;
		}
	}

	&__container {
		&-1 {
			display: flex;
			flex-direction: column;
			align-items: center;
		}
	}

	&__title {
		padding-bottom: 10px;
		color: #ffffff;
		font-size: 24px;
		font-weight: 700;
		@include adaptive(1023) {
			font-size: 20px;
		}
		@include adaptive(767) {
			font-size: 14px;
		}
	}

	&__desc {
		padding-bottom: 30px;
		color: #4b4e6b;
		line-height: 1.8;
		font-size: 16px;
		@include adaptive(1023) {
			font-size: 14px;
		}
		@include adaptive(767) {
			font-size: 12px;
		}
	}

	&__nav {
		display: flex;
		align-items: center;
		justify-content: center;
		color: rgb(70, 128, 202);
		font-size: 16px;

		@include adaptive(1023) {
			font-size: 14px;
		}

		@include adaptive(767) {
			flex-wrap: wrap;
		}

		p{
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			align-items: center;

			@include adaptive(767) {
				font-size: 0;
			}
		}

		a {
			font-size: 16px;
			color: rgb(70, 128, 202);
			line-height: 2.273;
			padding: 0 10px;
			@include transition(color);
			@include adaptive(1023) {
				font-size: 14px;
			}
			@include adaptive(767) {
				display: flex;
				align-items: center;
				min-height: 25px;
				font-size: 12px;
			}

			&:hover {
				color: #ffffff;
				@include transition(color);
			}
		}

		&-item {
			display: flex;

			span {
				margin-left: 15px;
				margin-right: 15px;
				font-size: 16px;
				color: rgb(70, 128, 202);
				line-height: 2.273;
				@include adaptive(1023) {
					margin-left: 10px;
					margin-right: 10px;
					font-size: 14px;
				}
				@include adaptive(767) {
					font-size: 0;
				}
			}
		}

		&-link {
			font-size: 16px;
			color: rgb(70, 128, 202);
			line-height: 2.273;
			@include transition(color);
			@include adaptive(1023) {
				font-size: 14px;
			}
			@include adaptive(767) {
				display: flex;
				align-items: center;
				min-height: 25px;
				font-size: 12px;
			}

			&:hover {
				color: #ffffff;
				@include transition(color);
			}
		}
	}
}

.footer_description{
	h2{
		padding-bottom: 10px;
		color: #ffffff;
		font-size: 24px;
		font-weight: 700;
		@include adaptive(1023) {
			font-size: 20px;
		}
		@include adaptive(767) {
			font-size: 14px;
		}
	}

	h3{
		padding-bottom: 10px;
		color: #ffffff;
		font-size: 24px;
		font-weight: 700;
		@include adaptive(1023) {
			font-size: 20px;
		}
		@include adaptive(767) {
			font-size: 14px;
		}
	}

	p{
		padding-bottom: 30px;
		color: #4b4e6b;
		line-height: 1.8;
		font-size: 16px;
		@include adaptive(1023) {
			font-size: 14px;
		}
		@include adaptive(767) {
			font-size: 12px;
		}
	}
}

.light{
	.footer__title{
		color: #576576;
	}
}
